
.luminance-wrapper {
  background: 50% 100% / 50% 50% no-repeat radial-gradient(ellipse at bottom, var(--theme-inverse-color), transparent, transparent);
  -webkit-background-clip: text;
  color: transparent;
  font-size: 6vw;
  font-family: "Source Sans Pro", sans-serif;
  animation: reveal 3000ms ease-in-out forwards 200ms,
  glow 2500ms linear infinite 2000ms;
}

@keyframes reveal {
  80% {
    letter-spacing: 8px;
  }
  100% {
    background-size: 300% 300%;
  }
}

@keyframes glow {
  40% {
    text-shadow: 0 0 8px var(--theme-inverse-color);
  }
}

