@import url('https://fonts.googleapis.com/css?family=Terminal+Dosis&display=swap');


:root {
  // Values will be updated by Theme
  --theme-background-color: whitesmoke;
  --theme-inverse-color: #2a2a2a;
  --font-family: 'Terminal Dosis', serif;
  --boat-sea-color: "#2BE3FA";
  --boat-wave-color: "#fff";
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--theme-background-color);
  color: var(--theme-inverse-color);
  font-family: 'Terminal Dosis', serif;
  transition: background-color 0.5s linear, color 0.5s linear;
  height: 100vh;
}
