.space-text-main {
  display: flex;

  .space-text-letter {
    font-size: 4vw;
    margin: 0 15px;
    line-height: .7;
    text-shadow: 0 0 2px rgba(0, 0, 0, .45);
    animation: span 3s ease-in infinite alternate;
  }

  .space-text-spacing {
    display: inline-flex;
    height: 30px;
    width: 27px;
    /* border: 2.5px solid #FF1EAD; */
    border: 2.35px solid black;
    border-radius: 14px;
    box-shadow: 0 0 2px rgba(0, 0, 0, .75),
    inset 0 0 2px rgba(0, 0, 0, .45);

    animation: letter 3s ease-in-out infinite alternate;
  }
}

@keyframes span {
  0%, 30% {
    margin: 0 15px;
  }
  70%, 100% {
    margin: 0 5px;
  }
}

@keyframes letter {
  0%, 30% {
    width: 27px;
  }
  70%, 100% {
    width: 30vw;
  }
}